<template>
	<div>
		<div class="userWrap" v-for="(item,index) in arr">
			<div class="pic"><img :src="item.imgsrc"></div>
			<div class="content">
				<h2>{{item.name}}</h2>
				<h6><em>{{item.num}}</em>人关注</h6>
				<a href="javascript:;" @click="fn(index)" :class="{guanzhu: item.bool}">{{item.guanzhu}}</a>
				<span class="close" @click="des(index)">×</span>
				<p>{{item.content}}</p>
				<div class="comment">
					<span class="zan"><img src="../../assets/images/ic_like_full.png"><em>{{item.zan}}</em></span>
					<span class="pingLun"><img src="../../assets/images/ic_comment_full.png"><em>{{item.ping}}</em></span>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		data: function(){
			return {
				arr: [
					{
						name: "王勃",
						num: 385463,
						imgsrc: require("../../assets/images/images/4.pic_03.jpg"),
						guanzhu: "关注",
						content: "勃， 三尺微命， 一介书生。 无路请缨， 等终军之弱冠； 有怀投笔， 慕宗悫之长风。 舍簪笏于百龄， 奉晨昏于万里。 非谢家之宝树， 接孟氏之芳邻。 他日趋庭， 叨陪鲤对； 今兹捧袂， 喜托龙门。 杨意不逢， 抚凌云而自惜； 钟期既遇， 奏流水以何惭?",
						zan: 35,
						ping: 85,
						boll: false
					},
					{
						name: "李白",
						num: 35563,
						imgsrc: require("../../assets/images/images/4.pic_03.jpg"),
						guanzhu: "关注",
						content: "弃我去者，昨日之日不可留；乱我心者，今日之日多烦忧；长风万里送秋雁，对此可以酣高楼；蓬莱文章建安骨，中间小谢又请发；俱怀逸兴壮思飞，欲上青天揽明月；抽刀断水水更流，举杯消愁愁更愁；人生在世不称意，明朝散发弄扁舟。",
						zan: 325,
						ping: 853,
						boll: false
					},
					{
						name: "柳永",
						num: 35563,
						imgsrc: require("../../assets/images/images/4.pic_03.jpg"),
						guanzhu: "关注",
						content: "寒蝉凄切，对长亭晚，骤雨初歇，都门帐饮无绪，留恋处，兰舟催发。执手相看泪眼，竟无语凝噎。念去去，千里烟波，暮霭沉沉楚天阔。对情自古伤离别，更那堪冷落清秋节！今宵酒醒何处，杨柳岸，晓风残月。此去经年，应是良辰好景虚设，便纵有千种风情，更与何人说。",
						zan: 246,
						ping: 353,
						boll: false
					}
				]
			}
		},
		methods: {
			fn: function(i){
				this.arr[i].guanzhu = "已关注";
				this.arr[i].bool = true;
			},
			des: function(i){
				this.arr.splice(i,1);
			}
		}
		
	}
</script>
<style type="text/css">
	.userWrap{width:351px;border-radius:10px;box-shadow:0 0 5px #ccc;text-align:left;margin:0 auto;background-color:#fff;margin-bottom:15px;}
	.userWrap .pic{width:78px;float:left;}
	.userWrap .pic img{margin:17px 0 0 14px;width:50px;}
	.userWrap .content {width:239px;position:relative;margin-left:78px;}
	.userWrap .content h2{height:26px;line-height:26px;padding-top:20px;font-size:16px;font-weight:normal;}
	.userWrap .content h6{height:19px;line-height:19px;color:#999999;font-size:12px;font-weight:normal;margin-bottom:7px;}
	.userWrap .content p{font-size:14px;color:#000;line-height:20px;font-weight:normal;}
	.userWrap .content .close{position: absolute;right:-20px;top:10px;font-size:14px;}
	.userWrap .content a{display:inline-block;width:75px;height:28px;background-color:#42c055;position:absolute;top:29px;right:0px;font-size:14px;line-height:28px;text-align:center;text-decoration:none;color:#fff;border-radius:3px;border:1px solid #42c055;}
	.userWrap .content .guanzhu{color:#42c055;background-color:#fff;}
	.userWrap .content .comment{height:49px;position:relative;font-size:12px;color:#ccc;}
	.userWrap .content .comment img{width:12px;}
	.userWrap .content .comment span em{padding-left:10px;}
	.userWrap .content .comment .pingLun{position:absolute; left:53px; top:10px;}
	.userWrap .content .comment .zan{position:absolute; left:3; top:10px;}
</style>


