<template>
	<div class="swiper-container" :class="swiperid">
		<div class="swiper-wrapper"><!-- 轮播图的容器 -->
			<slot name="swiper-con"></slot>	
		</div>
		<div :class="{'swiper-pagination': pagination}" :style="{'text-align': paginationDiraction}"><!-- 分页器的容器 标签里为传入的样式 -->
			
		</div>
	</div>
</template>
<script type="text/javascript">
	import "@/assets/lips/js/swiper.js"/*引入js库*/
	
	export default{
		props: {
			swiperid: {//容器名称
				type: String,
				default: ''
			},
			effect: {//轮播方式
				type: String,
				default: 'slide'
			},
			loop: {//是否轮播
				type: Boolean,
				default: true
			},
			diraction: {//轮播方向
				type: String,
				default: 'horizantal'
			},
			autoplay: {//轮播间隔时间
				type: Number,
				default: 2000
			},
			paginationType: {//分页器的样式
				type: String,
				default: 'bullets'
			},
			pagination: {//分页器是否需要
				type: Boolean,
				default: true
			},
			paginationDiraction: {//分页器的位置
				type: String,
				default: 'center'
			}
		},
		mounted: function(){//钩子函数
			var _this = this;//this指vm实例
			new Swiper("."+_this.swiperid, {//创建一个swiper对象 并传入相应的参数
				loop: _this.loop,
				pagination: ".swiper-pagination",
				paginationType: _this.paginationType,
				autoplay: parseInt(_this.autoplay),
				diraction: _this.diraction,
				effect: _this.effect,
				autoplayDisableOnInteraction: false
			})
		}
	}
</script>
<style type="text/css">
	@import "../../assets/lips/css/swiper.min.css";
	.swiper-container img{width:375px;}
	.swiper-container{border-bottom:1px solid #8b898b;}
</style>


